@import "./style/_vars.scss"; @import "./style/_mixins.scss"; @import "./spritesmith-generated/sprite.scss";
.contactCard {
  $this: &;

  background: $light-light-grey;
  @include shadow();
  text-align: center;
  padding: 50px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__image {
    display: block;
    margin: 0 auto 30px;
    border-radius: 50%;
    overflow: auto;
  }

  &__name {
    font-size: 24px;
    font-weight: 600;
    color: $blue;
  }

  &__position {
    color: $grey;
    margin-bottom: 30px;
  }

  &__phone {
    font-weight: 500;
    margin-bottom: 10px;
  }

  &__email {
    color: $dark-grey;
  }

}
